<template>
  <div class="bindTitle">
    <div class="backIcon" @click="back"></div>
    <div class="title">{{msg ? $utils.splitStr(msg, 15) : ''}}</div>
  </div>
</template>
<script>
export default {
  props: {
    msg: {
      type: String
    }
  },
  data () {
    return {
      
    }
  },
  methods: {
    back () {
      this.$emit('broadCast', {
        action: 'back'
      })
    }
  },
  mounted () {
    console.log(this.msg)
  }
}
</script>
<style lang="scss">
  .bindTitle {
    height: .88rem;
    border-bottom: 1px solid #f5f5f5;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    .bindTitle {
      font-size: 34px;
    }
    .backIcon {
      position: absolute;
      left: 0;
      width: 100px;
      height: 100%;
      top: 0;
      background-image: url('../../../assets/img/black-left.png');
      background-repeat: no-repeat;
      background-position: center;
      background-size: 20px 35px;
    }
  }
</style>



