<template>
  <div class="scrollBox" @touchmove.prevent="" style="z-index: 12">
    <header-nav @broadCast="backFn" v-if="$utils.showNavBar()" msg="完善登录信息"></header-nav>
    <div class="bind_phone">
      <div class="iconSize banner">
        <img src="../../../assets/img/bind_phone/bind.png" alt="绑定">
        <div class="setRewardText">
          关联手机号<br>立得{{100 * commonState.inviteVcoin}}个金币
        </div>
      </div>
      <div class="login-box" :class="{isAndroid: $utils.isAndroid()}">
        <div class="login-item">
          <div class="login-left">
            <input class="login-input" v-model="phoneData.mobile" type="number" :pattern="$reg.PHONE_NUMBER" :placeholder="placeholder.phone">
          </div>
        </div>
        <div class="login-item" v-if="showCapture">
          <div class="login-left">
            <input class="login-input" v-model="phoneData.capture" type="text" :placeholder="placeholder.capture">
          </div>
          <div class="getImageCode login-right">
            <div class="iconSize" @click="timestamp=new Date().getTime()">
              <img :src="origin_url + timestamp" alt="图片地址">
            </div>
          </div>
        </div>
        <div class="login-item">
          <div class="login-left">
            <input class="login-input" v-model="phoneData.code" type="number" pattern="/\d{1,4}/" :placeholder="placeholder.code">
          </div>
          <div class="buttonText login-right" @click="getCode">{{buttonText}}</div>
        </div>
        <div class="login-item login-button" :class="{active: phoneData.mobile && phoneData.code && (showCapture && phoneData.capture || !showCapture)}" @click="submit">提交</div>
      </div>
    </div>
    <prompt-box
      :dropBol="showConfirm"
      @leftBtn="cancelFn"
      @rightBtn="confirmFn"
      leftTxt="返回重绑"
      rightTxt="手机号直接登录"
      contentTxt="该手机号和其他微信已绑定，<br>请更换手机号重绑，或直接使用<br>手机号登录。">
    </prompt-box>
  </div>
</template>
<script>
import HeaderNav from './bindTitle'
// import p
import {mapState, mapMutations} from 'vuex'
import PromptBox from '../../common/prompt-box'
export default {
  components: {
    HeaderNav,
    PromptBox
  },
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    this.origin_url = this.$config.currentApi + '/v1/passport/captcha?timestamp='
    return {
      showConfirm: false,
      timestamp: new Date().getTime(),
      couldSendMsg: true,
      placeholder: {
        phone: '手机号码',
        capture: '右侧图形验证码',
        code: '手机验证码'
      },
      showCapture: false,
      buttonText: '获取验证码',
      phoneData: {
        mobile: '',
        code: '',
        capture: ''
      }
    }
  },
  beforeUpdate () {
    this.setInput()
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'common_set_guild',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_columnid_change',
      'common_set_hasRed'
    ]),
    // 获取用户信息
    getUserInfo (cb) {
      this.$api.get('GET_USERINFO', {}, res => {
        this.common_set_userInfo(Object.assign(this.commonState.userInfo, res.data, {phone: this.commonState.phone}))
        typeof cb === 'function' && cb()
      })
    },
    setInput () {
      if (this.phoneData.mobile) {
        this.phoneData.mobile = this.phoneData.mobile.replace(/[^0-9]/g, '')
        if (this.phoneData.mobile.length > 11) {
          this.phoneData.mobile = this.phoneData.mobile.substr(0, 11)
        }
      }
      if (this.phoneData.code) {
        this.phoneData.code = this.phoneData.code.replace(/[^0-9]/g, '')
        if (this.phoneData.code.length > 11) {
          this.phoneData.code = this.phoneData.code.substr(0, 4)
        }
      }
    },
    // 点击返回
    backFn () {
      this.$emit('closeBind', {from: 'bind_phone', action: '返回'})
      // this.confirmFn()
    },
    cancelFn () {
      this.phoneData = {
        mobile: '',
        capture: '',
        code: ''
      }
      clearInterval(this.timer)
      this.timer = null
      this.buttonText = '获取验证码'
      this.showCapture = false
      this.showConfirm = false
    },
    confirmFn () {
      this.$emit('closeBind', {from: 'bind_phone', action: '手机号直接登录', mobile: this.phoneData.mobile})
    },
    submit () {
      let couldRequest = true
      if (!couldRequest) {
        return
      }
      if (!this.$utils.isReg('PHONE_NUMBER', this.phoneData.mobile)) {
        this.$toast({
          message: '请输入正确手机号',
          position: 'middle',
          duration: 1500
        })
      } else if (this.showCapture && this.phoneData.capture === '') {
        this.$toast({
          message: '请输入图形验证码',
          position: 'middle',
          duration: 1500
        })
      } else if (!this.phoneData.code) {
        this.$toast({
          message: '请输入手机验证码',
          position: 'middle',
          duration: 1500
        })
      } else {
        couldRequest = false
        setTimeout(() => {
          couldRequest = true
        }, 2000)
        let data = {
          account: this.phoneData.mobile,
          code: this.phoneData.code,
          captcha_code: this.phoneData.capture
        }
        this.$api.post('BIND_MOBIE_NEW', data, res => {
          couldRequest = false
          if (res.data.user_token) {
            this.$utils.cache('user_token', res.data.user_token)
          }
          if (res.data.coin > 0) {
            let shareSuccessObj = Object.assign({type: 5}, res.data)
            this.set_shareSuccessObj({
              type: 7,
              shareSuccessObj
            })
            this.set_shareType(7)
            this.setShowShare(true)
            this.showBindPhone = false
          } else {
            this.$toast({
              message: '成功绑定手机号',
              duration: 1500
            })
          }
          this.$emit('closeBind', {
            from: 'bind_phone',
            action: '绑定成功'
          })
        }, res => {
          couldRequest = false
          if (res.code === 666) {
            this.showConfirm = true
          } else if (res.code === 706 || res.code === 705) {
            this.back()
          }
        }, res => {
          couldRequest = false
        })
      }
    },
    getCode () {
      if (!this.couldSendMsg) return
      if (this.buttonText !== '获取验证码') {
        return
      }
      // 两s后恢复可点击状态
      setTimeout(() => {
        this.couldSendMsg = true
      }, 2000)
      if (!this.$utils.isReg('PHONE_NUMBER', this.phoneData.mobile)) {
        this.$toast({
          message: '请输入正确手机号',
          position: 'middle',
          duration: 1500
        })
      } else if (this.showCapture && this.phoneData.capture === '') {
        this.$toast({
          message: '请输入图形验证码',
          position: 'middle',
          duration: 1500
        })
      } else {
        let data = {}
        data.mobile = this.phoneData.mobile
        if (this.showCapture) {
          data.captcha_code = this.phoneData.capture
        }
        this.couldSendMsg = false
        this.$api.get('SMSCODE', data, (res) => {
          this.$toast({
            message: res.msg,
            duration: 1500
          })
          this.numTxt = 60
          this.buttonText = `重新发送(${this.numTxt}s)`
          this.timer = setInterval(() => {
            this.numTxt--
            this.buttonText = `重新发送(${this.numTxt}s)`
            if (this.numTxt <= 0) {
              clearInterval(this.timer)
              this.couldSendMsg = true
              this.buttonText = '获取验证码'
            }
          }, 1000)
        }, (res) => {
          if (res.msg === '请输入图形验证码') {
            this.showCapture = true
            this.couldSendMsg = true
          }
          if (res.msg === '请输入图形验证码' || res.msg === '图形验证码不正确' || res.msg === '手机验证码不正确') {
            this.timestamp = new Date().getTime()
            this.couldSendMsg = true
          }
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  .bind_phone {
    background: #f5f5f5;
    height: 100vh;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding-top: 30px;
    box-sizing: border-box;
    .banner {
      height: 206px;
      position: relative;
      .setRewardText {
        font-size: 46px;
        line-height: 65px;
        color: #fff;
        font-weight: 900;
        position: absolute;
        left: 36px;
        top: 40px;
      }
    }
    .login-box {
      padding-top: 60px;
      width: 637px;
      margin: 0 auto;
      &.isAndroid {
        .login-input {
          line-height: .64rem!important;
        }
      }
      .login-item {
        width: 100%;
        border-radius: 80px;
        height: 80px;
        border: 1px solid#CCCCCC;
        margin-bottom: 40px;
        background: #fff;
        display: flex;
        align-items: center;
        overflow: hidden;
        .login-left {
          display: block;
          font-size: 30px;
          height: .6rem;
          flex: 1;
          .login-input {
            display: block;
            box-sizing: border-box;
            width: 100%;
            font-size: 30px;
            padding: 0 44px;
            line-height: .6rem;
          }
        }
        .login-right {
          display: flex;
          width: 214px;
          height: .6rem;
          border-left: 1px solid #ccc;
          display: flex;
          justify-content: center;
          min-width: 0;
          align-items: center;
          height: 36px;
          .iconSize {
            width: 163px;
          }
          
        }
        .buttonText {
          font-size: 30px;
          color: #FF4112;
        }
        &.login-button {
          background: #ff572e;
          opacity: .7;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #fff;
          font-size: 32px;
          &.active {
            opacity: 1;
          }
        }
      }
    }
  }
}
</style>



