<template>
  <div class="scrollBox" @touchmove.prevent="" style="z-index: 20">
    <header-nav @broadCast="backFn" v-if="$utils.showNavBar()" msg="完善登录信息"></header-nav>
    <div class="bind_phone">
      <div class="iconSize banner">
        <img src="../../../assets/img/bind_phone/bind.png" alt="绑定">
        <div class="setRewardText">
          关联微信号<br>立得{{100 * commonState.inviteVcoin}}个金币
        </div>
      </div>
      <div class="bindWechatButton" @click="bindWechat">
        <div class="iconSize">
          <img src="../../../assets/img/personal/login_wechat.png" alt="微信">
        </div>
        <div class="bind-text">立即关联</div>
      </div>
    </div>
    <prompt-box
      :dropBol="showConfirm"
      @leftBtn="cancelFn"
      @rightBtn="confirmFn"
      leftTxt="返回重绑"
      rightTxt="微信直接登录"
      contentTxt="该手机号和其他微信已绑定，<br>请更换手机号重绑，或直接使用<br>手机号登录。">
    </prompt-box>
  </div>
</template>
<script>
import HeaderNav from './bindTitle'
// import p
import {mapState, mapMutations} from 'vuex'
import PromptBox from '../../common/prompt-box'
export default {
  components: {
    HeaderNav,
    PromptBox
  },
  computed: mapState({
    commonState: state => state.common,
    share: state => state.share
  }),
  data () {
    this.origin_url = this.$config.currentApi + '/v1/passport/captcha?timestamp='
    return {
      showConfirm: false,
      timestamp: new Date().getTime(),
      couldSendMsg: true,
      placeholder: {
        phone: '手机号码',
        capture: '右侧图形验证码',
        code: '手机验证码'
      },
      showCapture: false,
      buttonText: '获取验证码',
      phoneData: {
        mobile: '',
        code: '',
        capture: ''
      }
    }
  },
  beforeUpdate () {
    this.setInput()
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'common_set_guild',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_columnid_change',
      'common_set_hasRed'
    ]),
    // 获取用户信息
    getUserInfo (cb) {
      this.$api.get('GET_USERINFO', {}, res => {
        this.common_set_userInfo(Object.assign(this.commonState.userInfo, res.data))
        typeof cb === 'function' && cb()
      })
    },
    setInput () {
      if (this.phoneData.mobile) {
        this.phoneData.mobile = this.phoneData.mobile.replace(/[^0-9]/g, '')
        if (this.phoneData.mobile.length > 11) {
          this.phoneData.mobile = this.phoneData.mobile.substr(0, 11)
        }
      }
      if (this.phoneData.code) {
        this.phoneData.code = this.phoneData.code.replace(/[^0-9]/g, '')
        if (this.phoneData.code.length > 11) {
          this.phoneData.code = this.phoneData.code.substr(0, 4)
        }
      }
    },
    // 点击返回
    backFn () {
      // this.$emit('closeBind', {from: 'bind_phone', action: '返回'})
      this.$emit('closeBind', {from: 'bind_phone', action: '返回'})
    },
    cancelFn () {
      this.showConfirm = false
      this.$emit('closeBind', {from: 'bind_wechat', action: '返回重绑'})
    },
    confirmFn () {
      this.$emit('closeBind', {from: 'bind_wechat', action: '微信直接登录', mobile: this.phoneData.mobile})
    },
    bindWechat () {
      this.$utils.plusReady(plus => {
        if (this.commonState.oauthInfo['weixin']) {
          var w = plus.nativeUI.showWaiting('认证中')
          setTimeout(() => {
            w.close()
          }, 5000)
          this.commonState.oauthInfo['weixin'].authorize(res => {
            let data = {
              code: res.code
            }
            this.$api.post('BIND_WECHAT_NEW', data, res => {
              w.close()
              if (res.data.user_token) {
                this.$utils.cache('user_token', res.data.user_token)
              }
              if (res.data.coin > 0) {
                let shareSuccessObj = Object.assign({type: 3, tipText: `已成功将微信${this.$utils.splitStr(res.data.wechat_nickname)}关联到当前账户上`}, res.data)
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj
                })
                this.set_shareType(16)
                this.setShowShare(true)
              } else {
                this.$emit('closeBind', {
                  from: 'bind_wechat',
                  action: '绑定成功'
                })
                this.$toast({
                  message: `已成功将微信${this.$utils.splitStr(res.data.wechat_nickname)}关联到当前账户上`,
                  duration: 1500
                })
              }
              this.getUserInfo()
            }, res => {
              w.close()
              if (res.code === 666) {
                this.showConfirm = true
              }
            }, catchErr => {
              w.close()
            })
          }, error => {
            w.close()
            if (error.code === -2) {
              plus.nativeUI.toast('用户取消授权')
            } else {
              plus.nativeUI.toast('拉取授权失败:' + JSON.stringify(error))
            }
          })
        } else {
          this.$toast('暂未获取到微信授权，请稍后再试！')
        }
      })
    }
  },
  watch: {
    'share.showSuccessShare' (flag) {
      if (!flag) {
        if (this.share.shareType === 16) {
          this.$emit('closeBind', {
            from: 'bind_wechat',
            action: '绑定成功'
          })
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  .bind_phone {
    background: #f5f5f5;
    height: 100vh;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding-top: 30px;
    box-sizing: border-box;
    .banner {
      height: 206px;
      position: relative;
      .setRewardText {
        font-size: 46px;
        line-height: 65px;
        color: #fff;
        font-weight: 900;
        position: absolute;
        left: 36px;
        top: 40px;
      }
    }
    .bindWechatButton {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #24B637;
      box-shadow: 0 2px 4px 0 #28BE3F;
      border-radius: 40.5px;
      width: 637px;
      height: 81px;
      color: #fff;
      margin: 100px auto;
      .iconSize {
        width: 57px;
        height: 43px;
      }
    }
  }
}
</style>



