<template>
  <div id="app" :class="{hidden: !commonState.isScroll}">
    <transition :name="transitionName">
      <keep-alive>
        <router-view class="Router" v-if="$route.meta.keepAlive"/>
      </keep-alive>
    </transition>
    <router-view class="Router" v-if="!$route.meta.keepAlive"/>
    <!-- 登录弹框 -->
    <mt-popup
      class="loginMask"
      v-model="commonState.login_box">
      <log-in v-show="commonState.login_box" @closeFn="close()"></log-in>
    </mt-popup>
    <!-- 用户协议 -->
    <transition name="drop">
      <div v-if="commonState.protocol_bol" class="agreement" style="z-index: 3000;">
        <login-agreement></login-agreement>
      </div>
    </transition>
    <shareSuccess v-if="share.showSuccessShare"></shareSuccess>
    <ConfirmExit v-if="showConfirm" :config="{confirmText:'返回'}" @closeConfirm='closeConfirm'></ConfirmExit>
    <OtherLogin v-if="commonState.other_login"></OtherLogin>
    <transition name='topMessage'>
      <topMessage v-if="showTopMessage" @closeMessage='closeMessage' :messageObj='messageObj'></topMessage>
    </transition>
    <download v-if="showDownload" @toInstall="toInstall" :isComplete="isComplete" :progress="progress"></download>
    <!-- 首页底部 -->
    <index-footer class="indexFotter" :imgIndex="commonState.imgIndex" v-show="commonState.showTabbar" v-if="$route.path === '/' || $route.path === '/make_money'  || $route.path === '/movie'  || $route.path === '/task'  || $route.path === '/personal_center'"></index-footer>
    <ad-popver v-if="commonState.showPopver" :top="commonState.clientY" @closeFn="closeFn"></ad-popver>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import IndexFooter from './components/common/index-footer'
// import LogIn from './components/personal-center/login'
import LogIn from './components/personal-center/new_login'
import loginAgreement from './components/common/login-agreement'
import {Base64} from 'js-base64'
import { Indicator } from 'mint-ui'
import shareSuccess from '@/components/share/share_success'
import ConfirmExit from '@/components/share/exit_tip_all'
import OtherLogin from '@/components/share/other_login'
import topMessage from '@/components/share/topMessage'
import VConsole from 'vconsole'
import Download from '@/components/share/download'
import AdPopver from '@/components/mutiAdList/pop'

export default {
  name: 'App',
  computed: mapState({
    commonState: state => state.common,
    share: state => state.share
  }),
  data () {
    return {
      transitionName: '',
      showTopMessage: false,
      showConfirm: false,       // 是否显示监听弹框
      showDownload: false,      // 显示下载进度
      messageObj: {},
      isComplete: false,        // 下载完成
      progress: 0,              // 下载进度
      messageList: [],
      localUrl: '',             // 下载本地文件相对地址
      messageShow: {}
    }
  },
  methods: {
    ...mapMutations([
      'common_login_bol',
      'common_login_box',
      'common_set_pushinfo',
      'common_set_shares',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_columnid_change',
      'common_set_backAction',
      'common_hide_tabbar',
      'common_set_from',
      'common_focus',
      'common_set_indexad',
      'common_oauthInfo',
      'common_set_uuid'
    ]),
    clearFirstStorage () {
      // var d = new Date().getTime()
      if (localStorage.getItem('menu')) {
        if (JSON.parse(localStorage.getItem('menu')).arr.length === 4) {
          localStorage.removeItem('menu')
        }
      }
      localStorage.removeItem('isTop')
      localStorage.removeItem('messageShow')
      localStorage.removeItem('domain')
      localStorage.removeItem('isShowUpdate')
    },
    closeFn () {
      this.$store.commit('common_set_showPopver', {
        showPopver: false,
        clientY: 0
      })
    },
    close () {
      this.common_login_box(false)
    },
    closeMessage () {
      this.showTopMessage = false
      setTimeout(() => {
        this.setMessageShow()
      }, 500)
    },
    initApp () {
      let version = window.plus.os.version.split('.')[0]
      if (this.$config.source_style >= 6) {
        let pushInfo = window.plus.push.getClientInfo()
        this.common_set_pushinfo(pushInfo)
        window.plus.push.addEventListener('receive', this.getMessage)
        window.plus.push.addEventListener('click', this.getClick)
        if (this.$config.source_style === 6) {
          if (version < 9) {
            window.plus.nativeUI.alert('你的手机系统版本低于iOS 9.0，掘金宝将无法正常使用，请及时升级最新系统。')
          }
        } else if (this.$config.source_style === 7) {
          if (version < 5) {
            window.plus.nativeUI.alert('你的手机系统版本低于android，掘金宝将无法正常使用，请及时升级最新系统。')
          }
        }
      }
    },
    getMessage (e) {
      let obj = e.content
      obj = JSON.parse(Base64.decode(obj))
      switch (obj.type) {
        case 'arc':
          let url = '/ArticleDetails/' + obj.data.aid
          // 添加推送文章获取奖励参数
          if (obj.data.call) {
            url = url + '?call=' + obj.data.call
          }
          if (this.$route.name === 'ArticleDetails') {
            this.$router.replace(url)
          } else {
            this.$router.push(url)
          }
          break
        case 'link':
          if (obj.data.url) {
            let url = obj.data.url
            if (url.indexOf('http') > -1) {
              window.plus.runtime.openURL(url)
            } else if (url.indexOf('#') > -1) {
              this.$router.push(url.substring(1, url.length))
            } else {
              this.$router.push(url)
            }
          }
          break
        case 'global':
          let shareSuccessObj = {
            pushTitle: obj.data.title,
            pushContent: obj.data.content,
            image: obj.data.image,
            url: obj.data.url
          }
          this.set_shareSuccessObj({
            type: 13,
            shareSuccessObj
          })
          this.set_shareType(13)
          this.setShowShare(true)
          break
        default : window.plus.nativeUI.toast('暂无该推送类型')
      }
    },
    getDomain () {
      this.$api.get('SHARE_DOMAIN', {}, res => {
        if (res.data.domain && res.data.domain.length > 0) {
          localStorage.setItem('domain', res.data.domain[0])
          let domain = res.data.domain[0]
          // if (domain[domain.length - 1] === '/') {
          //   domain = domain.substr(0, domain.length - 1)
          // }
          this.$config.currentWebUrl = domain
          this.$config.androidDownloadUrl = res.data.domain[0] + '?download_path=/download/index.html'
          this.$config.downloadUrl = res.data.domain[0] + '?download_path=/download/index.html'
        }
      })
    },
    // 获取透传点击事件
    getClick (e) {
      let obj = e.payload
      switch (obj.type) {
        case 'arc':
          let url = '/ArticleDetails/' + obj.data.aid
          // 添加推送文章获取奖励参数
          if (obj.data.call) {
            url = url + '?call=' + obj.data.call
          }
          if (this.$route.name === 'ArticleDetails') {
            this.$router.replace(url)
          } else {
            this.$router.push(url)
          }
          break
        case 'link':
          if (obj.data.url) {
            let url = obj.data.url
            if (url.indexOf('http') > -1) {
              window.plus.runtime.openURL(url)
            } else if (url.indexOf('#') > -1) {
              this.$router.push(url.substring(1, url.length))
            } else {
              this.$router.push(url)
            }
          }
          break
        case 'global':
          let shareSuccessObj = {
            pushTitle: obj.data.title,
            pushContent: obj.data.content,
            image: obj.data.image,
            url: obj.data.url
          }
          this.set_shareSuccessObj({
            type: 13,
            shareSuccessObj
          })
          this.set_shareType(13)
          this.setShowShare(true)
          break
        default: console.log(obj.type)
          break
      }
    },
    mountedInit () {
      if (this.$config.source_style === 6 || this.$config.source_style === 7) {
        if (window.plus) {
          this.initApp()
        } else {
          document.addEventListener('plusready', () => {
            this.initApp()
          })
        }
      }
    },
    // 获取分享plus对象
    getSharePlus () {
      if (this.$config.source_style >= 6) {
        this.$utils.plusReady(plus => {
          this.common_set_uuid(plus.device.uuid)
          plus.share.getServices(service => {
            let shares = {}
            for (let i in service) {
              let s = service[i]
              shares[s.id] = s
            }
            this.common_set_shares(shares)
          })
        })
      }
    },
    // 是否显示大礼包
    setShowGift () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.timer = setTimeout(() => {
        // 新用户，未领取大礼包，今天是否提醒过
        let showBigGifts = this.$utils.cache('showBigGifts') ? JSON.parse(this.$utils.cache('showBigGifts')) : null
        // 如果缓存存在并且缓存时间戳小于当前时间戳可弹起
        if (showBigGifts && showBigGifts.timeStamp < this.$utils.getTimeStamp()) {
          this.$utils.cache('showBigGifts', null)
          showBigGifts = null
        }
        if (((this.commonState.userInfo.is_new === 1 && this.commonState.userInfo.achieved_gift_bag === 0) || !this.commonState.login_bol) && !showBigGifts && this.$route.path === '/') {
          this.$store.commit('setShowShare', true)
          this.$store.commit('set_shareType', 9)
          this.$utils.cache('showBigGifts', JSON.stringify({
            timeStamp: this.$utils.getTodayStamp()
          }))
        }
        if (this.commonState.login_bol && this.commonState.userInfo.achieved_gift_bag === 1 && this.$route.path === '/' && !showBigGifts) {
          this.common_set_indexad(true)
        }
      }, 1000)
    },
    doJump () {
      // 从后台唤醒，失去焦点不显示ios文章评论输入浮框
      this.common_focus(false)
      this.args = window.plus.runtime.arguments
      // this.args = '{"path":"/task", "action":"jump"}'
      // this.args = 'path=/task&action=jump'
      if (this.args) {
        // urlScheme打开app
        if (typeof this.args === 'string' && this.args.indexOf('//') > -1) {
          if (this.args.indexOf('tencent') > -1 || this.args.indexOf('platformId') > -1 || this.args.indexOf('oauth') > -1 || this.args.indexOf('sdkversion') > -1) {
            return
          }
          let temp = this.args
          let path = temp.split('//')[1].split('=')[1]
          this.$router.push(path)
        // a=1&b=2 传参方式
        } else if (typeof this.args === 'string' && this.args.indexOf('&') > -1) {
          this.args = this.$qs.parse(this.args)
          if (this.args.action) {
            this.common_hide_tabbar(false)
            switch (this.args.action) {
              case 'login':
                this.common_set_from('ios')
                this.$router.push('/login')
                break
              case 'jump':
                this.$router.push(this.args.path)
                break
              default: break
            }
          }
        // json传参打开
        } else {
          if (typeof this.args === 'string') {
            this.args = JSON.parse(this.args)
          }
          if (this.args.action) {
            this.common_hide_tabbar(false)
            switch (this.args.action) {
              case 'login':
                this.common_set_from('ios')
                this.$router.push('/login')
                break
              case 'jump':
                this.$router.push(this.args.path)
                break
              default: break
            }
          }
        }
      } else {
        this.common_hide_tabbar(true)
      }
    },
    // 微信登录 成功提示
    showSuccess () {
      if (this.$utils.cache('showLoginSuccess')) {
        setTimeout(() => {
          this.$toast({
            message: '登录成功',
            duration: 1500
          })
          this.$utils.cache('showLoginSuccess', null)
        }, 1000)
      }
    },
    // 获取路由参数
    getArgument () {
      this.$utils.plusReady(() => {
        this.doJump()
      })
      document.addEventListener('newintent', res => {
        this.doJump()
      })
    },
    closeConfirm ({action}) {
      // 退出不需要缓存
      // this.$utils.cache('showConfirm', true)
      if (action === 'confirm') {
        this.showConfirm = false
      } else if (action === 'cancel') {
        this.$utils.cache('isTop', null)
        window.plus.runtime.quit() // 那么就退出app
      } else if (action === 'toSee') {
        this.showConfirm = false
        this.$router.replace('/task')
      }
    },
    // 获取未读消息
    setMessageShow () {
      if (this.messageList.length > 0) {
        var arr = ['/', '/make_money', 'task', '/personal_center']
        let flag = false
        for (let v of arr) {
          if (this.$route.path === v) {
            flag = true
            break
          }
        }
        if (flag) {
          this.messageObj = this.messageList[0]
          this.messageList.shift()
          this.messageShow[this.messageObj.id] = true
          this.showTopMessage = true
          this.$utils.cache('messageShow', JSON.stringify(this.messageShow))
        }
      }
    },
    getUnread_message () {
      if (!this.$utils.cache('user_token')) {
        return
      }
      this.$api.get('UNREADMESSAGE', {}, res => {
        if (res.data && res.data.length > 0) {
          let arr = []
          let d = document.createElement('div')
          for (let v of res.data) {
            if (!this.messageShow[v.id]) {
              d.innerHTML = v.content
              v.content = d.innerText
              arr.push(v)
            }
          }
          d = null
          this.messageList = arr
          this.setMessageShow()
        }
      })
    },
    // 设置返回监听
    setBack () {
      let that = this
      // 首页返回键处理
      function plusReady () {
        window.nativeNewBack = function () {
          // 首次按键，提示再按一次退出应用’
          if (that.commonState.login_box) {
            that.common_login_box(false)
          } else {
            if (!that.commonState.backAction) {
              if (that.$route.path === '/task' || that.$route.path === '/personal_center' || that.$route.path === '/make_money' || that.$route.path === '/movie') {
                that.$router.replace('/')
              } else if (that.$route.path === '/') {
                if (that.commonState.columnid * 1 !== 0) {
                  that.common_set_columnid_change()
                } else {
                  if (!that.$utils.cache('showConfirm')) {
                    that.showConfirm = true
                  }
                }
              } else {
                history.go(-1)// 回退到上一页面
              }
            } else {
              that.common_set_backAction()
            }
          }
        }
        window.plus.key.removeEventListener('backbutton', window.nativeNewBack)
        setTimeout(() => {
          window.plus.key.addEventListener('backbutton', window.nativeNewBack)
        }, 200)
      }
      if (window.plus) {
        plusReady()
      } else {
        document.addEventListener('plusready', plusReady, false)
      }
    },
    // 初始化定时器
    initTimer () {
      if (this.messageTimer) {
        clearInterval(this.messageTimer)
        this.messageTimer = null
      }
      this.getUnread_message()
      this.messageTimer = setInterval(() => {
        this.getUnread_message()
      }, 20000)
    },
    // 
    installApp (path) {
      this.isComplete = true
      window.plus.runtime.install(path, {
        force: true
      })
    },
    toInstall () {
      this.installApp(this.localUrl)
    },
    // 创建下载任务
    createDownLoad () {
      let localUrl = '_downloads/juejinbao.apk'
      window.plus.io.resolveLocalFileSystemURL(localUrl, entry => {
        // 该文件存在则删除
        entry.remove()
        this.acturallyDownLoad()
      }, () => {
        this.acturallyDownLoad()
      })
    },
    acturallyDownLoad () {
      let downloadUrl = 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn/juejinbao.apk'
      if (this.$config.source_style === 6) {
        
      } else if (this.$config.source_style === 7) {
        downloadUrl = 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn/juejinbao.apk'
      }
      
      let localUrl = '_downloads/juejinbao.apk'
      // 测试服务器下载地址
      if (this.$config.source_style === 7 && !this.$config.product) {
        downloadUrl = 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/dev_juejinbao.apk'
        localUrl = '_downloads/dev_juejinbao.apk'
      }
      window.plus.downloader.clear()
      let d = window.plus.downloader.createDownload(downloadUrl, {}, (d, status) => {
        if (status === 200) {
          this.isComplete = true
          this.localUrl = localUrl
        }
      })
      d.addEventListener('statechanged', res => {
        this.progress = Math.round(res.downloadedSize / res.totalSize * 100) ? Math.round(res.downloadedSize / res.totalSize * 100) : 0
      }, false)
      this.isComplete = false
      this.showDownload = true
      d.start()
    },
    clearLocalStorage () {
      if (this.$config.source_style >= 6) {
        this.$utils.cache('arcTop', null)
      }
    },
    // 退出登录
    dropFn () {
      this.dropBol = false
      this.common_login_bol(false)
      let data = {}
      if (this.commonState.pushInfo && this.commonState.pushInfo.clientid) {
        data.getui_cid = this.commonState.pushInfo.clientid
      }
      this.$api.get('LOGOUT', data, () => {
        this.$router.replace('/personal_center')
        this.$utils.cache('personalHeight', null)
        this.$toast({
          message: '退出成功',
          duration: 1500
        })
        this.$utils.cache('handOperated', 1)
        this.isExit = false
        this.common_login_bol(false)
        this.common_set_userInfo({})
        this.common_index_column(true)
        this.$utils.cache('threeCount', null)
        this.userInfo = {}
        this.user_icon = ''
        this.$utils.clearCache()
      })
    },
    nativeJump (path) {
      this.common_hide_tabbar(false)
      this.$router.push(path)
    },
    // 获取授权服务
    getOauth () {
      var obj = {}
      if (this.$config.source_style >= 6) {
        this.$utils.plusReady(plus => {
          plus.oauth.getServices(service => {
            service.map(item => {
              obj[item.id] = item
            })
            this.common_oauthInfo(obj)
          })
        })
      }
    }
  },
  created () {
    window.dropFn = this.dropFn
    window.shareFn = this.shareFn
    window.nativeJump = this.nativeJump
    // this.shareFn('qq', '{"title":"掘金宝0006即将把宝马X6免费提回家了！快来看看！","content":"赚豪车赚洋房，回家过年提车就靠它了，赶紧看看>","thumbs":["http://jjlmobile.oss-cn-shenzhen.aliyuncs.com/goods_car/gid_33304/pic_list_h/bf7733fc63a11e963db4624c3f9d948f.jpg?x-oss-process=image/resize,h_50"],"href":"http://wechat.juejinbaonews.fun?path=%2F&inviteCode=6812939"}', '首页')
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' (flag) {
      if (!this.commonState.login_box && !this.commonState.login_bol && this.commonState.reload_address) {
        this.$router.push({path: this.commonState.reload_address})
      }
      if (!this.commonState.login_box && !this.commonState.login_bol) {
        this.$toast({
          message: '取消登录',
          duration: 1500
        })
      }
      if (flag) {
        this.$toast({
          message: '请先登录',
          duration: 1500
        })
      }
    },
    'commonState.downloadChange' () {
      this.createDownLoad()
    },
    'commonState.login_bol' () {
      this.getUnread_message()
    },
    'commonState.isDebug' (flag) {
      if (flag) {
        this.vConsole = new VConsole()
      } else {
        this.vConsole.destroy()
      }
    },
    '$route' (to, from) {
      this.getOauth()
      if (to.path === '/' && (from.name === 'ArticleDetails' || from.name === 'CommonChannel')) {
        this.transitionName = 'slide-right'
        this.$nextTick(() => {
          this.transitionName = ''
        })
      } else {
        this.transitionName = ''
      }
      if (!this.commonState.shares['weixin']) {
        this.getSharePlus()
      }
      var arr = ['/', '/make_money', 'task', '/personal_center']
      let flag = false
      for (let v of arr) {
        if (this.$route.path === v) {
          flag = true
          break
        }
      }
      if (flag) {
        this.initTimer()
      } else {
        clearInterval(this.messageTimer)
        this.messageTimer = null
      }
      this.setShowGift()
      // 显示加载图标
      if (to.name === 'SelectCity') {
        Indicator.open()
      }
      // 背景色做融合
      // if (this.$config.source_style >= 6) {
      //   if (this.$route.meta.bgcolor) {
      //     window.plus.navigator.setStatusBarBackground(this.$route.meta.bgcolor)
      //   } else {
      //     window.plus.navigator.setStatusBarBackground('#fa0')
      //   }
      //   if (this.$route.name === 'samllVideoDetail') {
      //     window.plus.navigator.setFullscreen(true)
      //   } else {
      //     window.plus.navigator.setFullscreen(false)
      //   }
      // }
    },
    'commonState.changeBack' () {
      this.setBack()
    }
  },
  components: {
    LogIn,
    loginAgreement,
    shareSuccess,
    ConfirmExit,
    OtherLogin,
    topMessage,
    Download,
    IndexFooter,
    AdPopver
  },
  mounted () {
    this.clearLocalStorage()
    if (!this.$utils.cache('isRemoveHistory')) {
      this.$utils.cache('historys', null)
      this.$utils.cache('menu', null)
      this.$utils.cache('isRemoveHistory', true)
    }
    this.getOauth()
    // this.doJump()
    this.messageShow = this.$utils.cache('messageShow') ? JSON.parse(this.$utils.cache('messageShow')) : {}
    this.mountedInit()
    this.getSharePlus()
    this.setShowGift()
    this.getArgument()
    this.showSuccess()
    this.setBack()
    this.initTimer()
    this.getDomain()
    if (this.$config.source_style >= 6) {
      this.clearFirstStorage()
    }
    window.mui.init({
      gestureConfig: {
        tap: true, // 默认为true
        doubletap: true, // 默认为false
        longtap: true, // 默认为false
        swipe: true, // 默认为true
        drag: true, // 默认为true
        hold: false, // 默认为false，不监听
        release: false // 默认为false，不监听
      }
    })
  }
}
</script>

<style lang="scss" type="text/css">
#app{
  font-family: 'PingFangSC-Semibold', 'Microsoft YaHei', 'Helvetica', 'Arial', sans-serif;
  color: #232323;
  overflow: auto;
  position: relative;
  z-index: 0;
  min-height: 100vh;
  &.hidden {
    height: 100%;
    overflow: hidden;
  }
  .indexFotter {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
  }
  .loginMask{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transform: translate3d(0, 0, 0);
    background: none;
  }
  .agreement{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
}
</style>



