<template>
  <div class="maskBox" @touchmove.prevent="" style="z-index: 1000" @click="closeFn">
    <div class="adWrap" ref="content" :style="{top: `${top - contentHeight - 5}px`}" @click.stop="">
      <div class="close" @click.stop="deleteFn"></div>
      <div class="adright">
        <div class="adTitle">{{adObj.title}}</div>
        <div class="adDesc">{{adObj.desc}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    adObj: {
      default () {
        return {
          title: '不感兴趣',
          desc: '减少此类广告推荐'
        }
      },
      type: Object
    },
    top: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      title: '',
      contentHeight: ''
    }
  },
  methods: {
    // 删除
    deleteFn () {
      this.$store.commit('common_set_showPopver', {
        showPopver: false,
        clientX: 0,
        isClose: true
      })
    },
    // 关闭
    closeFn () {
      this.$emit('closeFn')
    }
  },
  mounted () {
    this.contentHeight = this.$refs.content.offsetHeight
  }
}
</script>
<style scoped lang='scss'>
.adWrap {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  width: 704px;
  height: 122px;
  background: url('../../assets/img/adview/close_pack.png') no-repeat center;
  background-size: 100%;
  box-sizing: border-box;
  display: flex;
  padding-left: 30px;
  box-sizing: border-box;
  padding-top: 16px;
  .close {
    width: 32px;
    height: 32px;
    background: url('../../assets/img/adview/circle_close.png') no-repeat center;
    background-size: 100% 100%;
    margin-right: 20px;
    margin-top: 25px;
  }
  .adTitle {
    font-size: 30px;
    color: #333333;
    line-height: 40px;
  }
  .adDesc {
    font-size: 26px;
    line-height: 34px;
    color: #666;
    margin-top: 4px;
  }
}
</style>



